.profile {
  margin: 5rem auto;
  box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
  width: 40rem;
  border-radius: 8px;
  padding: 1rem;
  text-align: center;
  background-color: #f4f0fa;
}


@media (max-width: 665px) {
  .profile {
    margin: 2rem auto;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.2);
    font-size: 0.7rem;
    width: 30rem;
    border-radius: 8px;
    padding: 1rem;
    text-align: center;
    background-color: #f4f0fa;
  }
  
}